import React from "react";
import $ from 'jquery';
import Homes_url from './Homes_url';
import Api_urls from './Api_urls';
import Login_check from './Login_check';

let URL_link=Api_urls();
let home_url_link=Homes_url();
let user_login=parseInt(sessionStorage.getItem("user_login"));
let user_username=sessionStorage.getItem("user_username");
let first_name=sessionStorage.getItem("user_first_name");
let last_name=sessionStorage.getItem("user_last_name");


$(document).on('submit','.profile-widget #profileform',function(e){
  e.preventDefault();
  let datas =$(this).serialize();
  let pass=$(this).find('.inputpassword').val();
  let cofirmpass=$(this).find('.inputconfpassword').val();
  let Agree=$('#agree:checked').length;
   
  $("#loading_modal").addClass('show');
  if(pass !== cofirmpass){
    $("#loading_modal .titlemodalbodymsg").html("Password is no Match.");
  }else{
 
    if(Agree == '0'){

      $("#loading_modal .titlemodalbodymsg").html("Do not agree ignore terms and conditions.");
    
      }else{
        
          $.ajax({
          type: 'POST',
          url: URL_link+"admin/webapi/app/register",
          dataType: "json",
          data:datas,
          success:function(obj){
        
          $("#loading_modal .titlemodalbodymsg").html(obj.msg);
          if(obj.status === "success"){
          let res=obj.data;
          $("#loading_modal .modal-body>i").attr('class','fa fa-check-circle');
        
          setTimeout(function(){
            window.location.href=home_url_link+"login";
          },3000);
         
          }

          },
          });
      }

     }
  
  });


class Profile extends React.Component{
  
        constructor(props) {
          super(props);
          this.state = {
            error: null,
            isLoaded: false,
            items: [],
            value: ''
          };
        
        }

       
        render(){
            const { error, isLoaded, items } = this.state;

                return (
                  <div className="clientlogo profile-widget">
                      <Login_check/>
                    <div className="container">
                  <form className="formarea profileform" id="profileform">
                    <h1 className="col-xs-12 col-sm-12 col-md-12 col-lg-12 frmtitle">
                        Profile
                    </h1>
                  <div className="form-group">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  User Name
                  </label>
                  <input type="text" className="custominput username" name="username" value={user_username}/>
                  <div className="borders-left"></div>
                  </div>
                <div className="form-group">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  first name
                  </label>
                  <input type="text" name="first_name" value={first_name}  className="custominput first_name"/>
                  <div className="borders-left"></div>
                  </div>
                <div className="form-group">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  Last name
                  </label>
                  <input type="text" name="last_name" value={last_name}  className="custominput first_name"/>
                  <div className="borders-left"></div>
                  </div>
                
                   <div className="form-group submitbtsec">
                   <button className="btn btn-info submitbt" name="submit" type="submit">
                    Update
                    </button>

                   </div>
                </form>
                  </div>
                  </div>
                );
        }
    
}

export default Profile;