$(document).on("click",".faq-widget .faqbt",function(e){
    e.preventDefault();
    let parents=$(this).parent();
    let current=$(this);

    if(parents.find(".faqcontent").hasClass("active")){
        $(".faqcontent").removeClass("active");
        $(".faq-widget .faqbt i.fa").removeClass("fa-chevron-up");
        $(".faq-widget .faqbt i.fa").addClass("fa-chevron-down");
    }else{
        $(".faqcontent").removeClass("active");

        $(".faq-widget .faqbt i.fa").removeClass("fa-chevron-up");
        $(".faq-widget .faqbt i.fa").addClass("fa-chevron-down");
        parents.find(".faqbt i.fa").removeClass("fa-chevron-down");

        parents.find(".faqbt i.fa").addClass("fa-chevron-up");
        parents.find(".faqcontent").addClass("active");
        
    }
   
});

