import React from 'react';
import Homes_url from './Homes_url';

let home_url_link=Homes_url();

function get_route_name(){

  let windows=window.location;
  let slugname=""+windows.pathname+"";
  let regex ="/";
  let str =slugname.replace(regex, '');
  let result =str.split("/");
  return result;
}

class Default extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  render(){
   
let dataslug=get_route_name();
    
    return (<div className="client-logo error-weigt">
        <div className="container-fluid">
        <center>
          <h1 className="eror404">Error 404</h1>
          <h2 className="erro404-title">
          Sorry Page Is Not Found: {dataslug[1]}
          </h2>
          <a className="eror404href" href={home_url_link}>Click to Home</a>
        </center>
        </div>
       </div>);
  }
}
  
export default Default;