import React, { Component } from 'react';
import Homes_url from './Homes_url';
import Api_urls from './Api_urls';
import $ from 'jquery';

let redreacturl=Homes_url()+"login";
let URL_link=Api_urls();
let user_login=parseInt(sessionStorage.getItem("user_login"));

class Logout extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: []
          
        };
      }

      componentDidMount(data){
        $.ajax({
        type: 'post',
        url: URL_link+"admin/webapi/app/logout?user_id="+user_login,
        beforeSend:function(){
          $("#loading_modal").addClass('show');
        },
        success:function(result){
  
        this.setState({
        isLoaded: true,
        items: result
        });
        }.bind(this),
        error:function(){
        console.log("error");
        this.setState({
        isLoaded: true,
        error:false,
        });
        }.bind(this)
        });
    }
    
    renderSquare(){
      const { error, isLoaded, items }=this.state;

      if(error){
        return (""); 
      }else{

        if(items.status === "Success"){
       
          sessionStorage.clear();
          localStorage.clear();
          window.location.href=redreacturl;
          
        }else{
          sessionStorage.clear();
          localStorage.clear();
          
          window.location.href=redreacturl;
          return (""); 
        }
     
      }

    }

    render(){

    const { error, isLoaded, items }=this.state;

            return (
              <div>
                {this.renderSquare(items)}
              </div>
            );

    }   
}   
export default Logout;