import React from 'react';
import Homes_url from './Homes_url';

let home_url_link=Homes_url();


function User_is_login(){
    let user_login_is=sessionStorage.getItem("user_login_is");
    let user_login=parseInt(sessionStorage.getItem("user_login"));
    if(user_login_is =='1' && user_login){
       window.location=home_url_link+"profile";
    }

    return '';

}

export default User_is_login;