import React from "react";
import $ from 'jquery';
import Homes_url from './Homes_url';
import Api_urls from './Api_urls';
import User_is_login from './User_is_login';

let URL_link=Api_urls();
let home_url_link=Homes_url();
let user_login=parseInt(sessionStorage.getItem("user_login"));

$(document).on('keyup','.signup-widget .inputconfpassword',function(e){
  e.preventDefault();
  let inputpassword=$(".signup-widget .inputpassword").val();
  let cnformpassword=$(this).val();
  let htmls="";
  
  if(cnformpassword === inputpassword){
    htmls+="";
    $('#singupform button').prop("disabled", false);
  }else{
    $('#singupform button').prop("disabled", true);

     htmls+="Password is no match...";
  }
  
  $("#confrmmsg").html(htmls);
  
  });

$(document).on('submit','.signup-widget #singupform',function(e){
  e.preventDefault();
  let datas =$(this).serialize();
  let pass=$(this).find('.inputpassword').val();
  let cofirmpass=$(this).find('.inputconfpassword').val();
  let Agree=$('#agree:checked').length;
   
  $("#loading_modal").addClass('show');
  if(pass !== cofirmpass){
    $("#loading_modal .titlemodalbodymsg").html("Password is no Match.");
  }else{
 
    if(Agree == '0'){

      $("#loading_modal .titlemodalbodymsg").html("Do not agree ignore terms and conditions.");
    
      }else{
        
          $.ajax({
          type: 'POST',
          url: URL_link+"admin/webapi/app/register",
          dataType: "json",
          data:datas,
          success:function(obj){
        
          $("#loading_modal .titlemodalbodymsg").html(obj.msg);
          if(obj.status === "success"){
          let res=obj.data;
          $("#loading_modal .modal-body>i").attr('class','fa fa-check-circle');
        
          setTimeout(function(){
            window.location.href=home_url_link+"login";
          },3000);
         
          }

          },
          });
      }

     }
  
  });


class Signup extends React.Component{
  
        constructor(props) {
          super(props); 
          this.state = {
            error: null,
            isLoaded: false,
            items: [],
            value: ''
          };
          this.handleChange = this.handleChange.bind(this);
        }

        handleChange(event) {
          this.setState({value: event.target.value});
        }
      

        render(){
            const { error, isLoaded, items } = this.state;

                return (
                  <div className="clientlogo signup-widget">
                    <User_is_login/>
                    <div className="container">
                  <form className="formarea" id="singupform">
                    <h1 className="col-xs-12 col-sm-12 col-md-12 col-lg-12 frmtitle">
                      Broker Register
                      </h1>
                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>First Name <small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput first_name" name="first_name"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-user-plus iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>
                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Last Name <small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput last_name" name="last_name"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-user-plus iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>
                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>User Name <small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput username" name="username"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-user-plus iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>
                 <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Email <small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput email" name="email"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-envelope iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>

                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Password<small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput inputpassword" name="password"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-lock iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>

                  <div className="form-group">
                  <span id="confrmmsg" className="col-xs-12 col-sm-12 col-md-12 col-lg-12 confrmmsg"></span>
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Confirm Password<small>(required)</small></span>
                  </label>
                  <input type="text" className="custominput inputconfpassword" name="conformpassword"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-lock iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>

                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Phone </span>
                  </label>
                  <input type="text" className="custominput phone" name="phone"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-phone-square iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>

                  <div className="form-group">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                 Address
                  </label>
                  <textarea className="custominput address" name="address"rows="3" onChange={this.handleChange}  value={this.state.value} />
                  </div>

                  <div className="form-group agreesec">
                  <label className="agreeseclable">
                    I agree to the <a className="agreesehyper" href={home_url_link+"privacy-policy"}>terms and conditions</a>
                  </label>
                    <input id="agree" type="checkbox" className="customcheckbox agree" name="agree_policy" value="1"/>
                   </div>

                   <div className="form-group submitbtsec">
                   <button className="btn btn-info submitbt" name="submit" type="submit">
                    Open Account
                     </button>

                   </div>
                </form>
                  </div>
                  </div>
                );
        }
    
}

export default Signup;