import React from "react";
import $ from 'jquery';
import Homes_url from './Homes_url';
import Api_urls from './Api_urls';

let URL_link=Api_urls();
let home_url_link=Homes_url();
let user_login=parseInt(sessionStorage.getItem("user_login"));


$(document).on('submit','#signinform',function(e){
  e.preventDefault();
  let datas =$(this).serialize();

      $("#loading_modal").addClass('show');

      $.ajax({
      type: 'POST',
      url: URL_link+"admin/webapi/app/login",
      dataType: "json",
      data: datas,
      success:function(obj){
      $("#loading_modal .titlemodalbodymsg").html(obj.msg);
      if(obj.status === "success"){
      let res=obj.data;
      $("#loading_modal .modal-body>i").attr('class','fa fa-check-circle');
      sessionStorage.setItem("user_login",res.id+"BWYSL");
      sessionStorage.setItem("user_idlogins","BWYSL"+res.id+"235");
      sessionStorage.setItem("user_username",res.username);
      sessionStorage.setItem("user_email",res.email);
      sessionStorage.setItem("user_image",res.avatar);
      sessionStorage.setItem("user_first_name",res.first_name);
      sessionStorage.setItem("user_last_name",res.last_name);
      sessionStorage.setItem("user_login_is",'1');

      setTimeout(function(){
        window.location.href=home_url_link+"home";
      },3000);
      
      }

      },
      });
  
  });
class Signin extends React.Component{
  
        constructor(props) {
          super(props);
          this.state = {
            error: null,
            isLoaded: false,
            items: [],
            value: ''
          };
        
        }

       
        render(){
            const { error, isLoaded, items } = this.state;

                return (
                  <div className="clientlogo signup-widget">
                    <div className="container">
                  <form className="formarea signinform" id="signinform">
                    <h1 className="col-xs-12 col-sm-12 col-md-12 col-lg-12 frmtitle">
                    Broker Login
                    </h1>
                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Email</span>
                  </label>
                  <input type="email" className="custominput username" name="email"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-envelope iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>
                  <div className="form-group">
                  <div className="formgroups">
                  <label className="col-xs-12 col-sm-12 col-md-12 col-lg-12 inputtitle">
                  <span>Password</span>
                  </label>
                  <input type="text" className="custominput password" name="password"/>
                  </div>
                  <div className="righticonsfom">
                  <i className="fa fa-lock iconsfom"></i>
                  </div>
                  <div className="borders-left"></div>
                  </div>
                  <div className="form-group agreesec">
                  <label className="agreeseclable">
                   <a className="agreesehyper" href={home_url_link+"forget-password"}>
                     Forget your Password</a>
                  </label>
                   </div>

                   <div className="form-group submitbtsec">
                   <button className="btn btn-info submitbt" name="submit" type="submit">
                    Login
                     </button>

                   </div>
                </form>
                  </div>
                  </div>
                );
        }
    
}

export default Signin;