import React from 'react';
import Homes_url from './Homes_url';
import Navication_nav from './Navication_nav';

var home_url_link=Homes_url();
let user_login_is=sessionStorage.getItem("user_login_is");
let user_login=parseInt(sessionStorage.getItem("user_login"));


class Navication extends React.Component{
    render(){
 
      const  navication_nav=Navication_nav();
      let get_usernav=function(){

        if(user_login_is =='1' && user_login){
          let data =[{
              id:"1",
              url:home_url_link+"admin/dashboard",
              name:"Dashboard",
              class:"item active",
           },{
            id:"1",
            url:home_url_link+"log-out",
            name:"Log Out",
            class:"item login",
         }];
           return data;
        }else{
          let data =[{
            id:"1",
            url:home_url_link+"login",
            name:"Login",
            class:"item login",
         },{
              id:"1",
              url:home_url_link+"sign-up",
              name:"Signup",
              class:"item active",
           }];

           return data;
      }
         
}
        return (
            <ul className="navication">
                {get_usernav().map((item,idx)=>(
                <li key={idx} className="items">
                <a className={item.class}href={item.url}>
                {item.name}
                </a>
                </li>
                ))
                }
                {navication_nav.map((item,idx)=>(
                <li key={idx} className="items">
                <a className={item.class}href={item.url}>
                {item.name}
                </a>
                </li>
                ))
                }
            </ul>
            );
            
    }

 }

 export default Navication;